<template>
	<view>
		<view class="tabr" :style="{top:headerTop}">
			<view :class="{on:typeClass=='goods'}" @tap="switchType('goods')">已评价</view>
			<view :class="{on:typeClass=='shop'}" @tap="switchType('shop')">待评价</view>
			<view class="border" :class="typeClass"></view>
		</view>
		<view class="place"></view>

		<view class="keep-main">
			<!-- 已评价 -->
			<view class="list" v-show="subState==1" v-for="(eva,evaNum) in goodsList" :key="evaNum">
				<view class="list-one">
					<view class="one-left">
						<image :src="eva.goods_logo"></image>
						<view class="evaluate">
							<text>{{eva.id}}</text>
							<text>{{eva.comment_star}}</text>
						</view>
					</view>
					<view class="one-right">
						{{eva.create_at}}
					</view>
				</view>
				<view class="list-two">
					{{eva.comment_content}}
				</view>
				<view class="list-three" v-for="(src,index) in eva.comment_covers">
					<image src="src"></image>
				</view>
				<view class="list-four">
					<view class="four-left">
						<image  src="eva.goods_logo"></image>
					</view>
					<view class="four-right">
						<text>{{eva.comment_content}}</text>
						<text>{{eva.price_selling}}</text>
					</view>
				</view>
			</view>
			<view class="do-evaluate" v-show="subState==2" v-for="(eva,index) in goodsList" :key="index">
				<view class="do-evaluate-one">
					<view class="evaluate-left">
						{{eva.order_no}}
					</view>
					<view class="evaluate-right">已完成</view>
				</view>
				<view class="do-evaluate-two">
					<image src="eva.goods_logo"></image>
					<view class="goods-dec">
						<text class="goods-title">
							{{eva.goods_title}}
						</text>
						<view class="goods-price">
							<text>共{{eva.number}}件产品 &nbsp合计：</text>
							<view class="total-price">
								<text>￥</text>
								<text>{{eva.price_real}}</text>
							</view>
						</view>

					</view>
				</view>
				<view class="do-evaluate-three" @click="sayFeel()">
					<text>去评价</text>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		mounted(){
			var _this=this
			uni.request({
				url:this.config.url+"member/comment",
				method:"post",
				data:{
					token:this.token,
					type:this.subState,
				},
				success(res) {
					console.log(res)
					_this.goodsList=res.data.data.data;
				}
			})
		},
		data() {
			return {
				goodsList: [],
				headerTop: 0,
				//控制滑动效果
				typeClass: 'goods',
				subState: 1,
				theIndex: null,
				oldIndex: null,
				isStop: false
			}
		},
		onPageScroll(e) {

		},
		//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
		onPullDownRefresh() {
			setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 1000);
		},
		onLoad() {
			//兼容H5下排序栏位置
			// #ifdef H5
			//定时器方式循环获取高度为止，这么写的原因是onLoad中head未必已经渲染出来。
			let Timer = setInterval(() => {
				let uniHead = document.getElementsByTagName('uni-page-head');
				if (uniHead.length > 0) {
					this.headerTop = uniHead[0].offsetHeight + 'px';
					clearInterval(Timer); //清除定时器
				}
			}, 1);
			// #endif
		},
		methods: {
			sayFeel() {
				uni.navigateTo({
					url: "/pages/user/keep/sayFeel/sayFeel"
				})
			},
			switchType(type) {
				// if(this.typeClass==type){
				// 	return ;
				// }
				// uni.pageScrollTo({
				// 	scrollTop:0,
				// 	duration:0
				// })
				// this.typeClass = type;
				// this.subState = this.typeClass==''?'':'show'+type;
				// setTimeout(()=>{
				// 	this.oldIndex = null;
				// 	this.theIndex = null;
				// 	this.subState = this.typeClass=='goods'?'':this.subState;
				// },200)
				this.typeClass = type;
				if (type == "goods") {
					this.subState = 1;

				} else if (type == "shop") {
					this.subState = 2;
				}
			},


			discard() {
				//丢弃
			}


		}
	}
</script>
<style lang="scss">
	view {
		display: flex;
		flex-wrap: wrap;

	}

	html {
		background: white;
	}

	page {
		position: relative;
		background-color: #f5f5f5;
	}

	@font-face {
		font-family: "HMfont-home";
		src: url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2');
	}

	.icon {
		font-family: "HMfont-home" !important;
		font-size: 60upx;
		font-style: normal;
		color: #000000;

		&.jia {
			&:before {
				content: "\e641";
			}
		}

		&.jian {
			&:before {
				content: "\e643";
			}
		}

		&.shanchu {
			&:before {
				content: "\e6a4";
			}
		}

		&.shixiao {
			&:before {
				content: "\e669";
			}
		}

	}

	.hidden {
		display: none !important;
	}

	.place {
		width: 100%;
		height: 95upx;
	}

	.tabr {
		background-color: #fff;
		width: 94%;
		height: 95upx;
		padding: 0 3%;
		border-bottom: solid 1upx #dedede;
		position: fixed;
		top: 0;
		z-index: 10;

		view {
			width: 50%;
			height: 90upx;
			justify-content: center;
			align-items: center;
			font-size: 32upx;
			color: #999;
		}

		.on {
			color: rgba(20, 204, 33, 1);

		}

		.border {
			height: 4upx;
			background-color: rgba(20, 204, 33, 1);
			transition: all .3s ease-out;

			&.shop {
				transform: translate3d(100%, 0, 0);
			}
		}

	}

	.keep-main {
		display: flex;
		flex-direction: column;
		background: white;
		margin-top: 20upx;
	}

	.list {

		width: 92%;
		padding: 30upx 4% 0;
		margin-bottom: 30upx;
		background: white;

		.list-one {
			width: 100%;
			display: flex;
			justify-content: space-between;
			align-items: center;

			.one-left {
				display: flex;
				align-items: center;

				image {
					width: 86upx;
					height: 86upx;
					border-radius: 86upx;
					border: 1px solid rgba(54, 153, 255, 1);
				}

				.evaluate {
					margin-left: 30upx;
					display: flex;
					flex-direction: column;
					justify-content: space-around;
					align-items: center;
					font-size: 28upx;
					font-family: PingFang-SC-Regular;
					font-weight: 400;
					color: rgba(102, 102, 102, 1);
				}

			}

			.one-right {
				font-size: 24upx;
				font-family: DINPro-Light;
				font-weight: 300;
				color: rgba(153, 153, 153, 1);
				display: flex;
				justify-content: space-between;
				align-items: flex-start;
			}
		}

		.list-two {
			font-size: 28upx;
			font-family: PingFang-SC-Regular;
			font-weight: 400;
			color: rgba(51, 51, 51, 1);
			margin: 20upx 0;
		}

		.list-three {
			display: flex;
			align-items: center;
			margin: 10upx 0 30upx;

			image {
				width: 220upx;
				height: 220upx;
				margin-right: 10upx;
			}
		}

		.list-four {
			display: flex;
			padding: 20upx;
			justify-content: space-between;
			align-items: center;

			background: rgba(245, 245, 245, 1);

			.four-left {
				width: 140upx;
				height: 140upx;
          
				image {
					width: 140upx;
					height: 140upx;
				}
			}

			.four-right {

				margin-left: 10upx;
				display: flex;
				flex-direction: column;
				
				

				text:nth-child(1) {
					font-size: 28upx;
					color: rgba(51, 51, 51, 1);
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					text-overflow: ellipsis;
					overflow: hidden;

				}

				text:nth-child(2) {
					color: rgba(255, 32, 27, 1);
					font-size: 24upx;
				}
			}
		}
	}

	// 待评价
	.do-evaluate {
		display: flex;
		flex-direction: column;

		.do-evaluate-one {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 20upx 4%;
			border-bottom: 1px solid #cecece;

			.evaluate-left {
				font-size: 28upx;
				font-family: PingFang-SC-Regular;
				font-weight: 400;
				color: rgba(16, 16, 16, 1);
			}

			.evaluate-right {
				font-size: 28upx;
				font-family: PingFang-SC-Regular;
				font-weight: 400;
				color: rgba(20, 204, 33, 1);
			}
		}

		.do-evaluate-two {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin: 0 4%;
			padding: 20upx 0;
			border-bottom: 1px solid #cecece;

			image {
				width: 157upx;
				height: 157upx;
			}

			.goods-dec {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				align-content: flex-end;

				.goods-title {
					font-size: 28upx;
					font-family: PingFang-SC-Medium;
					font-weight: 500;
					color: rgba(16, 16, 16, 1);
				}

				.goods-price {
					display: flex;
					font-size: 24upx;
					color: rgba(102, 102, 102, 1);

					.total-price {
						text:nth-child(2) {
							font-size: 22upx;
							font-family: PingFang-SC-Bold;
							font-weight: bold;
							color: rgba(51, 51, 51, 1);
						}

						text:nth-child(1) {
							font-size: 16upx;
							font-family: PingFang-SC-Bold;
							font-weight: bold;
							color: rgba(51, 51, 51, 1);
						}
					}
				}
			}
		}

		.do-evaluate-three {

			width: 156upx;
			height: 56upx;
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 20upx 0 20upx 75%;
			font-size: 28upx;
			font-family: PingFang-SC-Regular;
			font-weight: 400;
			color: rgba(20, 204, 33, 1);
			border: 1px solid rgba(20, 204, 33, 1);
			border-radius: 56upx;
		}
	}
</style>
